.whitepaper-single .form__table {
  padding: 30px; }
  @media all and (max-width: 768px) {
    .whitepaper-single .form__table {
      padding: 15px; } }
  .whitepaper-single .form__table col:first-child {
    width: 100%; }
  .whitepaper-single .form__table col:last-child {
    width: 100%; }
  .whitepaper-single .form__table tr th, .whitepaper-single .form__table tr td {
    padding: 15px 0;
    display: block; }
  .whitepaper-single .form__table tr th {
    padding: 15px 0 0 0; }
  .whitepaper-single .form__table tr td textarea {
    height: 100px; }
    .whitepaper-single .form__table tr td textarea[name="division_only"] {
      height: 40px; }
  .whitepaper-single .form__table tr td .half input[type="text"] {
    width: 70%; }
  .whitepaper-single .form__table tr td.label {
    font-weight: bold;
    padding: 15px 0 0; }
  .whitepaper-single .form__table tr td.value {
    display: flex;
    align-items: center;
    padding: 10px 0; }
  .whitepaper-single .form__table tr td.comment {
    padding: 0 0 15px; }
  .whitepaper-single .form__table button {
    margin-top: 20px; }
    @media all and (max-width: 768px) {
      .whitepaper-single .form__table button {
        margin-top: 15px; } }

.whitepaper-single .form .button_area {
  flex-direction: column;
  gap: 15px; }

.whitepaper-single .article__ttl {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 50px; }
  @media all and (max-width: 768px) {
    .whitepaper-single .article__ttl {
      font-size: 20px;
      margin-bottom: 30px; } }

.whitepaper-single .article__img {
  text-align: center;
  margin-bottom: 60px; }
  @media all and (max-width: 768px) {
    .whitepaper-single .article__img {
      margin-bottom: 30px; } }
  .whitepaper-single .article__img img {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    max-width: 85%;
    margin: 0 auto; }

.whitepaper-single .article__lead {
  margin-bottom: 70px; }
  @media all and (max-width: 768px) {
    .whitepaper-single .article__lead {
      margin-bottom: 30px; } }

.whitepaper-single .article .wp-slick .slide-arrow {
  position: absolute;
  top: 40%;
  cursor: pointer;
  z-index: 101; }
  .whitepaper-single .article .wp-slick .slide-arrow.prev-arrow {
    left: 0;
    width: 30px;
    height: 30px;
    background: url("../img/common/slick-arrow-l.png") 50%/contain no-repeat; }
  .whitepaper-single .article .wp-slick .slide-arrow.next-arrow {
    right: 0;
    width: 30px;
    height: 30px;
    background: url("../img/common/slick-arrow-r.png") 50%/contain no-repeat; }

.whitepaper-single .toc__ttl {
  font-size: 22px;
  font-weight: bold;
  padding-bottom: 10px;
  border-bottom: 3px solid #6785C1;
  margin-bottom: 30px; }
  @media all and (max-width: 768px) {
    .whitepaper-single .toc__ttl {
      font-size: 18px;
      margin-bottom: 15px; } }

.whitepaper-single .toc__list {
  font-weight: bold;
  padding: 20px; }
  @media all and (max-width: 768px) {
    .whitepaper-single .toc__list {
      padding: 15px; } }
  .whitepaper-single .toc__list dl:not(:last-child) {
    margin-bottom: 30px; }
    @media all and (max-width: 768px) {
      .whitepaper-single .toc__list dl:not(:last-child) {
        margin-bottom: 15px; } }
  .whitepaper-single .toc__list dl dt {
    font-size: 22px;
    margin-bottom: 10px; }
    @media all and (max-width: 768px) {
      .whitepaper-single .toc__list dl dt {
        font-size: 16px; } }
  .whitepaper-single .toc__list dl dd {
    margin-bottom: 5px; }
    .whitepaper-single .toc__list dl dd ul {
      padding-left: 1.5em; }
      .whitepaper-single .toc__list dl dd ul li {
        list-style: disc; }

/*
  ----------------
  whitepaper
  ----------------
*/
.whitepaper .hero-m {
  background: url("../img/whitepaper/bg-hero.webp") 50%/cover no-repeat; }

.whitepaper .card {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  background: #fff; }
  .whitepaper .card:not(:last-child) {
    margin-bottom: 60px; }
    @media all and (max-width: 768px) {
      .whitepaper .card:not(:last-child) {
        margin-bottom: 30px; } }
  .whitepaper .card__img {
    width: 35%;
    background: #F1F1F1;
    padding: 45px;
    margin: 0; }
    @media all and (max-width: 768px) {
      .whitepaper .card__img {
        width: 100%;
        padding: 30px; } }
    .whitepaper .card__img img {
      max-width: none;
      width: auto;
      height: 186px;
      object-fit: contain;
      box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2); }
      @media all and (max-width: 768px) {
        .whitepaper .card__img img {
          height: auto;
          width: 100%; } }
  .whitepaper .card__box {
    width: 65%;
    padding: 30px; }
    @media all and (max-width: 768px) {
      .whitepaper .card__box {
        width: 100%;
        padding: 15px; } }
    .whitepaper .card__box--ttl {
      font-size: 20px;
      font-weight: bold;
      margin-bottom: 20px; }
      @media all and (max-width: 768px) {
        .whitepaper .card__box--ttl {
          font-size: 16px;
          margin-bottom: 15px; } }
    .whitepaper .card__box--txt {
      margin-bottom: 30px; }
      @media all and (max-width: 768px) {
        .whitepaper .card__box--txt {
          margin-bottom: 15px; } }
    .whitepaper .card__box .more {
      width: 300px;
      margin: 0;
      font-size: 16px;
      transition: all .3s; }
      @media all and (max-width: 768px) {
        .whitepaper .card__box .more {
          width: 85%;
          margin: 0 auto;
          font-size: 14px; } }
  .whitepaper .card:hover {
    opacity: 1;
    box-shadow: 0 0 10px rgba(59, 72, 111, 0.8); }
    .whitepaper .card:hover .more.bg-o {
      background: #fff;
      color: #F29600; }
      .whitepaper .card:hover .more.bg-o::before {
        background: url("../img/common/icon-arrow-o2.png") 50%/contain no-repeat; }
